import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { Grid, Link } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 450,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  Grid: {
      margin : '30px'
  }
}));

export default function Recipe({title,calories,image,url,cuisine,meal,dish,labels}) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  if(labels=="")
  {
    labels=labels+"None";
  }
  calories= "Calories : " + calories ;
  cuisine= "Cuisine : "+cuisine ;
  meal = "Meal : "+meal;
  dish = "Dish : "+dish;
  labels = "Label : "+labels;

  return (
    <Grid item xs={12} sm={4} >
    <Card className={classes.root} style={{margin: '2%'}}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            FB
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={title}
        
        subheader= {calories} 
      />
      <CardMedia
        className={classes.media}
        image={image}
        title={title}
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
        {cuisine}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
         {meal}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
         {dish}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
         {labels}
        </Typography>
       
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites" color="secondary" size="medium" >

          <FavoriteIcon />
        </IconButton>
    
            <Link href = {url} >Full Recipe</Link>
         

       </CardActions>
    </Card>
  </Grid>
  );
}
